<template>
    <div>
        <template v-show="!detailLoading">
            <!--该父元素用于已验真图片的绝对定位,适配imageUrl的图片大小在不同分辨率下高度不固定的问题-->
            <div style="position: relative;">
                <van-image v-show="detailJO.imageUrl" fit="contain" :src="detailJO.imageUrl" @click="preview()"/>
                <img class="authentic"
                     v-show="detailJO.ticketType === 'invoice' || detailJO.ticketType === 'train' || detailJO.ticketType === 'plane'"
                     src="../../assets/invoice/authentic.png"/>
            </div>
            <template v-if="detailJO.ticketType === 'invoice'">
                <van-cell-group title="开票内容">
                    <van-cell title="发票代码" v-model="detailJO.invoiceCode"/>
                    <van-cell title="发票号码" v-model="detailJO.invoiceNumber"/>
                    <van-cell title="开票日期" v-model="detailJO.billingDate"/>
                    <van-cell title="开票金额" v-model="detailJO.amount"/>
                    <van-cell title="不含税额" v-model="detailJO.amountExcludeTax"/>
                    <van-cell title="税额" v-model="detailJO.tax"/>
                    <van-cell title="校验码" v-model="detailJO.checkCode"/>
                </van-cell-group>
                <van-cell-group title="开票方">
                    <van-cell title="开票方" v-model="detailJO.sellerTaxName"/>
                    <van-cell title="纳税人识别号" v-model="detailJO.sellerTaxNumber"/>
                    <van-cell title="地址、电话" v-model="detailJO.sellerAddressPhone"/>
                    <van-cell title="开户行账号" v-model="detailJO.sellerBankAccount"/>
                </van-cell-group>
                <van-cell-group title="收票方">
                    <van-cell title="收票方" v-model="detailJO.buyerTaxName"/>
                    <van-cell title="纳税人识别号" v-model="detailJO.buyerTaxNumber"/>
                    <van-cell title="地址、电话" v-model="detailJO.buyerAddressPhone"/>
                    <van-cell title="开户行账号" v-model="detailJO.buyerBankAccount"/>
                </van-cell-group>
                <van-cell-group title="项目列表">
                    <van-cell-group v-for="(goods,index) in detailJO.goodsJA" :title="'项目'+(index+1)">
                        <van-cell title="名称" v-model="goods.goodsName"/>
                        <van-cell title="金额" v-model="goods.amountExcludeTax"/>
                        <van-cell title="税率" v-model="goods.taxRate"/>
                        <van-cell title="税额" v-model="goods.tax"/>
                    </van-cell-group>
                </van-cell-group>
            </template>
            <template v-else-if="detailJO.ticketType === 'train'">
                <van-cell title="姓名" v-model="detailJO.name"/>
                <van-cell title="金额" v-model="detailJO.amount"/>
                <van-cell title="开始时间" v-model="detailJO.startTime"/>
                <van-cell title="结束时间" v-model="detailJO.endTime"/>
                <van-cell title="开始地点" v-model="detailJO.startLocation"/>
                <van-cell title="结束地点" v-model="detailJO.endLocation"/>
                <van-cell title="车次" v-model="detailJO.trainNumber"/>
                <van-cell title="座位等级" v-model="detailJO.seatClass"/>
            </template>
            <template v-else-if="detailJO.ticketType === 'plane'">
                <van-cell title="姓名" v-model="detailJO.name"/>
                <van-cell title="金额" v-model="detailJO.amount"/>
                <van-cell title="开始时间" v-model="detailJO.startTime"/>
                <van-cell title="结束时间" v-model="detailJO.endTime"/>
                <van-cell title="开始地点" v-model="detailJO.startLocation"/>
                <van-cell title="结束地点" v-model="detailJO.endLocation"/>
                <van-cell title="客票代码" v-model="detailJO.planeCode"/>
                <van-cell title="航班号" v-model="detailJO.flightNumber"/>
                <van-cell title="承运人" v-model="detailJO.carrier"/>
                <van-cell title="座位等级" v-model="detailJO.seatClass"/>
                <van-cell title="客票序列号" v-model="detailJO.serialNumber"/>
            </template>
            <template v-else-if="detailJO.ticketType === 'manual'">
                <van-cell title="金额" v-model="detailJO.amount"/>
                <van-cell title="开票日期" v-model="detailJO.billingDate"/>
                <van-cell title="开始时间" v-model="detailJO.startTime"/>
                <van-cell title="结束时间" v-model="detailJO.endTime"/>
                <van-cell title="票据编号" v-model="detailJO.manualCode"/>
                <van-cell title="是否有章" v-model="detailJO.hasStamp"/>
                <van-cell title="备注信息" v-model="detailJO.memo"/>
            </template>
            <template v-else>
                <van-row v-show="!detailLoading">
                    <van-col span="24" style="text-align: center">类型错误，无法获取详细信息</van-col>
                </van-row>
            </template>
        </template>
        <van-loading style="width: 100%;text-align: center;padding: 50px 0;" color="#1989fa" v-show="detailLoading">加载中...</van-loading>
        <van-image-preview v-model="showImagePreview" :images="previewImages" :loop="false"></van-image-preview>
    </div>
</template>

<script>
    export default {
        name: "Detail",
        props: {},
        data() {
            return {
                detailLoading: false, detailJO: {},
                showImagePreview: false, previewImages: []
            }
        },
        methods: {
            getDetail() {
                this.detailLoading = true
                this.axios.get(mobileURL + "/r/jd?sid=" + sid + '&cmd=com.actionsoft.apps.addons.invoice.getInvoiceDetail' +
                    '&invoiceId=' + this.$route.query.invoiceId).then((res) => {
                    this.detailLoading = false
                    let ro = res.data
                    if (ro.result === 'ok') {
                        this.detailJO = ro.data.jo
                    } else {
                        this.$toast.fail(ro.msg);
                    }
                })
            }, preview() {
                this.previewImages[0] = this.detailJO.imageUrl
                this.showImagePreview = true
            }
        }, mounted() {
            this.getDetail()
        }
    }
</script>

<style scoped>
    .van-cell-group__title {
        color: #3fa3ff;
    }

    .authentic {
        width: 150px;
        position: absolute;
        top: calc(100% + 80px);
        left: 80px;
        transform: rotate(-45deg);
        z-index: 1;
    }
</style>