<template>
    <div>
        <keep-alive>
            <router-view ref="router"></router-view>
        </keep-alive>
        <van-tabbar route v-model="active" ref="tabbar">
            <van-tabbar-item name="successList" ref="successListItem" replace to="/successList" icon="passed">
                识别成功
            </van-tabbar-item>
            <van-button @click="addInvoice" class="add-invoice" icon="plus" :round="true" size="small"
                        style="position: relative;top: 11px;background: #4486fc;"></van-button>
            <van-tabbar-item name="unsuccessList" ref="unsuccessListItem" replace to="/unsuccessList" icon="more-o">
                识别中/识别失败
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>

    import {Notify} from 'vant';
    import Vue from 'vue'


    Vue.use(Notify);
    export default {
        name: "Home",
        data() {
            return {
                active: 'successList'
            }
        },
        methods: {
            addInvoice() {
                //formui拷贝的js
                //异步上传图片到微信服务器
                let syncUpload = (localIds) => {
                    const toast = this.$toast.loading({
                        duration: 0,       // 持续展示 toast
                        forbidClick: true, // 禁用背景点击
                        loadingType: 'spinner',
                        message: '正在上传...'
                    })
                    let uploadIndex = 0;
                    let invoiceIdArr = [];
                    let invoiceResult = {success: 0, fail: 0, processing: localIds.length}
                    localIds.forEach(localId => {
                        //有时会有回调两次的bug？此字段控制回调次数
                        let waitCallback1 = true;
                        wx.uploadImage({
                            localId: localId,
                            isShowProgressTips: false,
                            success: (res) => {
                                if (waitCallback1) {
                                    waitCallback1 = false;
                                    let serverId = res.serverId // 返回图片的服务器端ID
                                    let fileName = localId.substr(localId.lastIndexOf('/') + 1)
                                    //上传到微信
                                    this.axios.post('./jd?sid=' + sid + '&appId=' + appId + '&cmd=com.actionsoft.apps.addons.invoice.UPLOAD_WX&fileName=' +
                                        fileName + '&groupValue=' + invoiceUserId + '&fileValue=' + boItemName + '&serverId=' + serverId).then((res) => {
                                        //上传图片到三方发票识别系统
                                        this.axios.post('./jd?sid=' + sid + '&appId=' + appId +
                                            '&cmd=com.actionsoft.apps.addons.invoice.recognizeInvoice&fileName=' + fileName + '&groupValue=' + invoiceUserId + '&fileValue=' + boItemName).then((res) => {
                                            let ro = res.data
                                            if (ro.result === 'ok') {
                                                let invoiceId = ro.data.invoiceId
                                                invoiceIdArr.push(invoiceId);
                                            }
                                            // 多张情况下不关闭toast
                                            // else {
                                            //     this.$toast.fail('识别失败')
                                            // }
                                            uploadIndex++
                                            if (uploadIndex === localIds.length) {//所有图片上传完毕
                                                // 跳转到"UnsuccessList"，并且重新reload
                                                this.$toast.success('正在后台识别，您可以继续其他操作')
                                                let activeName = this.$refs.tabbar.$props.value;
                                                if (activeName === 'successList') {
                                                    this.$refs.unsuccessListItem.$el.click();
                                                    let unsuccessListComponent = this.$children.find(child => {
                                                        return child.$options.name === 'UnsuccessList'
                                                    })
                                                    if (unsuccessListComponent) {//如果识别中/识别失败组件已经加载过，则刷新，否则click之后会自动刷新
                                                        this.$nextTick(() => {
                                                            this.$refs.router.refresh()
                                                        })
                                                    }
                                                } else if (activeName === 'unsuccessList') {
                                                    this.$refs.router.refresh()
                                                }
                                                //后台刷新获取识别结果
                                                let getResult = () => {
                                                    this.axios.post('./jd?sid=' + sid + '&cmd=com.actionsoft.apps.addons.invoice.queryInvoiceResult&invoiceIdArrStr=' + JSON.stringify(invoiceIdArr)).then((res) => {
                                                        let ro = res.data
                                                        if (ro.result === 'ok') {
                                                            try {
                                                                if (ro.data.success === invoiceResult.success && ro.data.fail === invoiceResult.fail &&
                                                                    ro.data.processing === invoiceResult.processing) {
                                                                } else {
                                                                    invoiceResult.success = ro.data.success
                                                                    invoiceResult.fail = ro.data.fail
                                                                    invoiceResult.processing = ro.data.processing
                                                                    let sum = ro.data.success + ro.data.fail + ro.data.processing
                                                                    let duration = 3000;
                                                                    if (ro.data.processing === 0) {
                                                                        // duration = 0
                                                                    }
                                                                    //提示
                                                                    Notify({
                                                                        message: '识别成功 ' + ro.data.success + "/" + sum + "、识别失败 " + ro.data.fail + "/" + sum + "、识别中 " + ro.data.processing + "/" + sum,
                                                                        duration: duration,
                                                                        background: '#ff5722'
                                                                    });
                                                                }
                                                            } catch (e) {
                                                            }
                                                        }
                                                        if (ro.data.processing === 0) {
                                                            //识别完毕跳转到成功页，刷新列表
                                                            this.$refs.successListItem.$el.click();
                                                            let successListComponent = this.$children.find(child => {
                                                                return child.$options.name === 'SuccessList'
                                                            })
                                                            successListComponent.refresh()
                                                            let unsuccessListComponent = this.$children.find(child => {
                                                                return child.$options.name === 'UnsuccessList'
                                                            })
                                                            unsuccessListComponent.refresh()
                                                        } else {
                                                            //每6秒查询一次
                                                            setTimeout(getResult, 6000);
                                                        }
                                                    })
                                                }
                                                setTimeout(getResult, 3000);
                                            }
                                        })
                                    })
                                }
                            },
                            error: () => {
                                this.$toast.fail('上传到企业微信的请求出错')
                            }
                        })
                    })
                }
                if (isWeiXin === "true") {
                    //有时会有回调两次的bug？此字段控制回调次数
                    let waitCallback = true;
                    wx.chooseImage({
                        count: 9, // 默认9
                        sizeType: ['original'], // 可以指定是原图还是压缩图，默认二者都有
                        sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
                        // defaultCameraMode: "batch",//表示进入拍照界面的默认模式，目前有normal与batch两种选择，normal表示普通单拍模式，batch表示连拍模式，不传该参数则为normal模式。（注：用户进入拍照界面仍然可自由切换两种模式）
                        success: (res) => {
                            if (waitCallback) {
                                waitCallback = false;
                                syncUpload(res.localIds)
                            }
                        }
                    })
                }
                if (isMobileApp === "true") {
                    //移动门户
                    try {
                        var getUploadDestination = function () {
                            var url = document.URL;
                            var ret = "uf";
                            if (url && url.indexOf("/r/") != -1) {
                                ret = url.substring(0, url.indexOf("/r/")) + "/r/uf";
                            }
                            var finalUrl = ret + "?";
                            return finalUrl;
                        };
                        var SERVER = getUploadDestination() + "appId=" + appId + "&sid=" + sid + "&groupValue=" + invoiceUserId + "&fileValue=" +
                            boItemName + "&repositoryName=invoice";
                        var invoiceMobileAppParams = {
                            success: 'invoiceHome.mobileAppSuccessCallback',
                            fail: 'invoiceHome.mobileAppFailCallback',
                            count: 9,
                            sizeType: ['original'],
                            sourceType: ['album', 'camera'],
                            SERVER: encodeURI(SERVER)
                            // timeWaterFlag: config.timeWaterFlag,//是否添加时间水印
                            // sizeLimit: parseInt(fileMaxSize) * 1024 * 1024,//文件大小
                            // timeLimit: config.timeLimit,//时间限制
                        };
                        awsWebview.chooseImage(invoiceMobileAppParams)
                    } catch (e) {
                        alert(e)
                    }
                }
            },
            mobileAppSuccessCallback(params) {
                const toast = this.$toast.loading({
                    duration: 0,       // 持续展示 toast
                    forbidClick: true, // 禁用背景点击
                    loadingType: 'spinner',
                    message: '正在上传...'
                })
                let uploadIndex = 0;
                let invoiceIdArr = [];
                let invoiceResult = {success: 0, fail: 0, processing: params.length}
                params.forEach(param => {
                    //上传图片到三方发票识别系统
                    this.axios.post('./jd?sid=' + sid + '&appId=' + appId +
                        '&cmd=com.actionsoft.apps.addons.invoice.recognizeInvoice&fileName=' + param.fileName + '&groupValue=' + invoiceUserId +
                        '&fileValue=' + boItemName).then((res) => {
                        let ro = res.data
                        if (ro.result === 'ok') {
                            let invoiceId = ro.data.invoiceId
                            invoiceIdArr.push(invoiceId);
                        }
                        // 多张情况下不关闭toast
                        // else {
                        //     this.$toast.fail('识别失败')
                        // }
                        uploadIndex++
                        if (uploadIndex === params.length) {//所有图片上传完毕
                            // 跳转到"UnsuccessList"，并且重新reload
                            this.$toast.success('正在后台识别，您可以继续其他操作')
                            let activeName = this.$refs.tabbar.$props.value;
                            if (activeName === 'successList') {
                                this.$refs.unsuccessListItem.$el.click();
                                let unsuccessListComponent = this.$children.find(child => {
                                    return child.$options.name === 'UnsuccessList'
                                })
                                if (unsuccessListComponent) {//如果识别中/识别失败组件已经加载过，则刷新，否则click之后会自动刷新
                                    this.$nextTick(() => {
                                        this.$refs.router.refresh()
                                    })
                                }
                            } else if (activeName === 'unsuccessList') {
                                this.$refs.router.refresh()
                            }
                            //后台刷新获取识别结果
                            let getResult = () => {
                                this.axios.post('./jd?sid=' + sid + '&cmd=com.actionsoft.apps.addons.invoice.queryInvoiceResult&invoiceIdArrStr=' + JSON.stringify(invoiceIdArr)).then((res) => {
                                    let ro = res.data
                                    if (ro.result === 'ok') {
                                        try {
                                            if (ro.data.success === invoiceResult.success && ro.data.fail === invoiceResult.fail &&
                                                ro.data.processing === invoiceResult.processing) {
                                            } else {
                                                invoiceResult.success = ro.data.success
                                                invoiceResult.fail = ro.data.fail
                                                invoiceResult.processing = ro.data.processing
                                                let sum = ro.data.success + ro.data.fail + ro.data.processing
                                                let duration = 3000;
                                                if (ro.data.processing === 0) {
                                                    // duration = 0
                                                }
                                                //提示
                                                Notify({
                                                    message: '识别成功 ' + ro.data.success + "/" + sum + "、识别失败 " + ro.data.fail + "/" + sum + "、识别中 " + ro.data.processing + "/" + sum,
                                                    duration: duration,
                                                    background: '#ff5722'
                                                });
                                            }
                                        } catch (e) {
                                        }
                                    }
                                    if (ro.data.processing === 0) {
                                        //识别完毕跳转到成功页，刷新列表
                                        this.$refs.successListItem.$el.click();
                                        let successListComponent = this.$children.find(child => {
                                            return child.$options.name === 'SuccessList'
                                        })
                                        successListComponent.refresh()
                                        let unsuccessListComponent = this.$children.find(child => {
                                            return child.$options.name === 'UnsuccessList'
                                        })
                                        unsuccessListComponent.refresh()
                                    } else {
                                        //每6秒查询一次
                                        setTimeout(getResult, 6000);
                                    }
                                })
                            }
                            setTimeout(getResult, 3000);
                        }
                    })
                })
            },
            mobileAppFailCallback() {
                this.$toast.fail('上传图片出错')
            }
        },
        mounted() {
            window.invoiceHome = this
            this.$refs.successListItem.$el.click();
            if (isWeiXin === "true") {
                //注册微信
                //client.bpm.form.page.import.js.weixin.htm 拷贝的js
                var nonceStr = "abc";
                var timestamp = Date.parse(new Date()) + "";
                timestamp = timestamp.substring(0, 10);
                var url = window.location.href;
                url = url.substring(0, url.indexOf('#'));//url要和注册的url一样
                var string1 = "jsapi_ticket=" + jsapi_ticket + "&noncestr=" + nonceStr + "&timestamp=" + timestamp + "&url=" + url;
                var signature = sha1(string1);
                wx.config({
                    beta: true,// 必须这么写，否则在微信插件有些jsapi会有问题
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    appId: corpId, // 必填，企业微信的corpID
                    timestamp: timestamp, // 必填，生成签名的时间戳
                    nonceStr: nonceStr, // 必填，生成签名的随机串
                    signature: signature,// 必填，签名，见[附录1](#11974)com.actionsoft.apps.workbench_mobile_h5_list_page
                    jsApiList: jsApiList // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
                });
                wx.ready(function () {
                    //wx.hideAllNonBaseMenuItem();
                    wx.hideMenuItems({
                        menuList: ["menuItem:share:timeline", "menuItem:favorite", "menuItem:share:appMessage", "menuItem:openWithQQBrowser", "menuItem:openWithSafari", "menuItem:share:email", "menuItem:share:wechat"] // 要隐藏的菜单项，所有menu项见附录3
                    });
                });
                wx.error(function (res) {
                    // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
                    alert("微信注册信息验证失败，请联系管理员。校验结果为：" + JSON.stringify(res));
                });
            }
        }
    }

</script>

<style scoped>
    .add-invoice .van-icon {
        color: #ffffff;
    }
</style>