<template>
    <div style="position: relative;background-color: #ebebeb;">
        <van-row style="background-color:﻿ #4386fc;color: #ffffff;padding: 10px 10px 5px;font-size: 16px;">
            <van-col span="18">总金额</van-col>
            <van-col span="6">张数</van-col>
        </van-row>
        <van-row style="background-color:﻿ #4386fc;color: #ffffff;padding: 10px 10px 30px;">
            <van-col span="18" style="font-size: 28px;font-weight: bold">
                ￥{{this.sumWithCommas}}
            </van-col>
            <van-col span="6" style="padding-top: 10px;">{{this.count}}</van-col>
        </van-row>
        <form action="/"> <!--Tips: 在 van-search 外层增加 form 标签，并且 action 不为空，即可在 IOS 弹出的输入法中显示搜索按钮-->
            <van-search placeholder="搜索" v-model="keyword" @search="reloadList"/>
        </form>
        <van-dropdown-menu>
            <van-dropdown-item v-model="invoiceType" :options="invoiceTypeOptions" @change="reloadList"/>
            <van-dropdown-item v-model="reimburseStatus" :options="reimburseStatusOptions" @change="reloadList"/>
            <van-dropdown-item :title="currentDateTitle" ref="invoiceDateDropdownItem">
                <van-datetime-picker v-model="currentDate" type="year-month" :min-date="minDate" :max-date="maxDate"
                                     :formatter="yearMonthFormatter" @confirm="confirm" @cancel="cancel" :show-toolbar="true"
                                     cancel-button-text="清除"/>
            </van-dropdown-item>
        </van-dropdown-menu>
        <van-pull-refresh v-model="refreshLoading" @refresh="refresh">
            <van-list style="min-height:calc(100vh - 271px)" v-model="loading" :finished="finished" @load="loadList" finished-text="没有更多了"
                      error-text="加载出错">
                <ul>
                    <li v-for="(item,invoiceIndex) in list" v-bind:key="item.id">
                        <router-link :to="{path:'./detail',query:{invoiceId:item.id}}" append>
                            <van-swipe-cell class="my-swipe-cell">
                                <template v-if="item.ticketType === 'invoice'">
                                    <div class="invoice-item">
                                        <van-row>
                                            <van-col span="18">
                                                <van-row>
                                                    <van-col span="24" class="buyerTaxName"> 付款方: {{item.buyerTaxName}}</van-col>
                                                </van-row>
                                                <van-row style="margin:10px 0;">
                                                    <van-col span="24" class="sellerTaxName">{{item.sellerTaxName}}</van-col>
                                                </van-row>
                                            </van-col>
                                            <van-col span="6" class="amount">
                                                ￥{{item.amount}}
                                            </van-col>
                                        </van-row>
                                        <van-row>
                                            <van-col span="6" class="invoiceType">发票</van-col>
                                            <van-col span="12" class="billingDate">{{item.billingDate}}</van-col>
                                            <van-col span="6" :class="'reimburseStatus ' + item.reimburseStatus">
                                                <template v-if="item.reimburseStatus === 'unused'">
                                                    可报销
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'using'">
                                                    报销中
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'used'">
                                                    已报销
                                                </template>
                                            </van-col>
                                        </van-row>
                                    </div>
                                </template>
                                <template v-else-if="item.ticketType === 'train'">
                                    <div class="invoice-item">
                                        <van-row>
                                            <van-col span="18">
                                                <van-row>
                                                    <van-col span="24" class="name"> {{item.name}}</van-col>
                                                </van-row>
                                                <van-row style="margin:10px 0;">
                                                    <van-col span="24" class="trainNumber">{{item.trainNumber}} {{item.startLocation}} - {{item
                                                        .endLocation}}
                                                    </van-col>
                                                </van-row>
                                                <van-row style="margin:10px 0;">
                                                    <van-col span="24" class="time">{{item.startTime}}<br/>{{item.endTime}}</van-col>
                                                </van-row>
                                            </van-col>
                                            <van-col span="6" class="amount">￥{{item.amount}}</van-col>
                                        </van-row>
                                        <van-row>
                                            <van-col span="6" class="invoiceType">火车票</van-col>
                                            <van-col span="12" class="billingDate">{{item.billingDate}}</van-col>
                                            <van-col span="6" :class="'reimburseStatus ' + item.reimburseStatus">
                                                <template v-if="item.reimburseStatus === 'unused'">
                                                    可报销
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'using'">
                                                    报销中
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'used'">
                                                    已报销
                                                </template>
                                            </van-col>
                                        </van-row>
                                    </div>
                                </template>
                                <template v-else-if="item.ticketType === 'plane'">
                                    <div class="invoice-item">
                                        <van-row>
                                            <van-col span="18">
                                                <van-row>
                                                    <van-col span="24" class="name"> {{item.name}}</van-col>
                                                </van-row>
                                                <van-row style="margin:10px 0;">
                                                    <van-col span="24" class="flightNumber">{{item.flightNumber}} {{item.startLocation}} - {{item
                                                        .endLocation}}
                                                    </van-col>
                                                </van-row>
                                                <van-row style="margin:10px 0;">
                                                    <van-col span="24" class="time">{{item.startTime}}<br/>{{item.endTime}}</van-col>
                                                </van-row>
                                            </van-col>
                                            <van-col span="6" class="amount">￥{{item.amount}}</van-col>
                                        </van-row>
                                        <van-row>
                                            <van-col span="6" class="invoiceType">飞机票</van-col>
                                            <van-col span="12" class="billingDate">{{item.billingDate}}</van-col>
                                            <van-col span="6" :class="'reimburseStatus ' + item.reimburseStatus">
                                                <template v-if="item.reimburseStatus === 'unused'">
                                                    可报销
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'using'">
                                                    报销中
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'used'">
                                                    已报销
                                                </template>
                                            </van-col>
                                        </van-row>
                                    </div>
                                </template>
                                <template v-else-if="item.ticketType === 'manual'">
                                    <div class="invoice-item">
                                        <van-row>
                                            <van-col span="18">
                                                {{item.memo}}
                                            </van-col>
                                            <van-col span="6" class="amount">￥{{item.amount}}</van-col>
                                        </van-row>
                                        <van-row>
                                            <van-col span="6" class="invoiceType">其他票</van-col>
                                            <van-col span="12" class="billingDate">{{item.billingDate}}</van-col>
                                            <van-col span="6" :class="'reimburseStatus ' + item.reimburseStatus">
                                                <template v-if="item.reimburseStatus === 'unused'">
                                                    可报销
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'using'">
                                                    报销中
                                                </template>
                                                <template v-else-if="item.reimburseStatus === 'used'">
                                                    已报销
                                                </template>
                                            </van-col>
                                        </van-row>
                                    </div>
                                </template>
                                <van-button square slot="right" type="danger" text="删除" @click.prevent="removeInvoice(item.id,invoiceIndex)"/>
                            </van-swipe-cell>
                        </router-link>
                    </li>
                </ul>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>

    export default {
        name: "SuccessList",
        data() {
            return {
                keyword: "", //搜索
                list: [], loading: false, finished: false, error: false, rowCount: 10, //list
                invoiceType: "",
                invoiceTypeOptions: [
                    {text: '全部类型', value: "", icon: 'http://localhost:8078/portal/console/images/logo.png'},
                    {text: '发票', value: "invoice", icon: 'https://b.yzcdn.cn/vant/icon-demo-1126.png'},
                    {text: '火车票', value: "train", icon: 'like-o'},
                    {text: '飞机票', value: "plane", icon: 'like-o'},
                    {text: '其他', value: "manual", icon: 'like-o'}
                ],//dropdownMenu
                reimburseStatus: "",
                reimburseStatusOptions: [
                    {text: '全部状态', value: ""},
                    {text: '可报销', value: "unused"},
                    {text: '报销中', value: "using"},
                    {text: '已报销', value: "used"}
                ],//dropdownMenu
                currentDate: new Date(), currentDateTitle: "日期", minDate: new Date(2016, 12, 30), maxDate: new Date(),//发票日期dateTimePicker
                startTime: '', endTime: '',//日期查询条件
                sum: 0, count: 0,//统计
                refreshLoading: false//下拉刷新
            }
        },
        methods: {
            loadList() {
                let url = mobileURL + '/r/jd?sid=' + sid + '&cmd=com.actionsoft.apps.addons.invoice.queryInvoiceList' +
                    '&skip=' + this.list.length + '&rowCount=10&invoiceType=' + this.invoiceType + "&reimburseStatus=" + this.reimburseStatus +
                    "&startTime=" + this.startTime + "&endTime=" + this.endTime + "&keyword=" + this.keyword
                this.axios.get(url).then((res) => {
                    let ro = res.data
                    if (ro.result === 'ok') {
                        if (ro.data.hasOwnProperty('sum')) {
                            this.sum = ro.data.sum
                            this.count = ro.data.count
                        }
                        this.list.push(...ro.data.list)
                        // 加载状态结束
                        this.loading = false
                        // 数据全部加载完成
                        if (ro.data.list.length === 0) {//不能用不足一页判断，只能多取一次因为有时候是9条（一次取10条，有9条符合条件）
                            this.finished = true
                        }
                    } else {
                        this.error = true
                    }
                    this.refreshLoading = false;//如果是下拉刷新，则置回false状态
                })
            },
            reloadList() {
                this.list = []
                this.loading = true
                this.finished = false
                this.loadList()
            },
            yearMonthFormatter(type, value) {
                if (type === 'year') {
                    return `${value}年`
                } else if (type === 'month') {
                    return `${value}月`
                }
                return value
            },
            cancel() {
                this.currentDateTitle = "不限时间"
                this.startTime = ''
                this.endTime = ''
                this.$refs.invoiceDateDropdownItem.toggle()
                this.reloadList()
            },
            confirm(val) {
                this.currentDateTitle = val.getFullYear() + "年" + (val.getMonth() + 1) + "月"
                this.startTime = val.getFullYear() + "-" + (val.getMonth() + 1) + "-" + "1 00:00:00"
                this.endTime = val.getFullYear() + "-" + (val.getMonth() + 1) + "-" + "31 23:59:59"
                this.$refs.invoiceDateDropdownItem.toggle()
                this.reloadList()
            },
            removeInvoice(invoiceId, index) {
                let deleteInvoice = this.list[index];
                if (deleteInvoice.reimburseStatus === 'used') {
                    this.$toast.fail('该票据已经报销，不允许删除');
                } else if (deleteInvoice.reimburseStatus === 'using') {
                    this.$toast.fail('该票据正在报销，不允许删除');
                } else {
                    this.$dialog.confirm({
                        title: '请确认',
                        message: '确认删除该票据吗'
                    }).then(() => {
                        this.$toast.loading({
                            duration: 0,       // 持续展示 toast
                            forbidClick: true, // 禁用背景点击
                            loadingType: 'spinner',
                            message: '正在删除...'
                        });
                        let url = mobileURL + '/r/jd?sid=' + sid + '&cmd=com.actionsoft.apps.addons.invoice.removeInvoice&invoiceId=' + invoiceId
                        this.axios.get(url).then((res) => {
                            let ro = res.data
                            if (ro.result === 'ok') {
                                this.$toast.success('删除成功');
                                let deletedInvoice = this.list[index];
                                //前端删除当前发票
                                this.list.splice(index, 1)
                                //前端修改总额和张数
                                this.sum -= deletedInvoice.amount
                                this.sum = this.sum.toFixed(2)
                                this.count -= 1
                            } else {
                                this.$toast.fail(ro.msg);
                            }
                        })
                    }).catch(() => {
                        // on cancel
                    });
                }
            },
            refresh() {
                this.reloadList()
            }
        },
        computed: {
            sumWithCommas() {//逗号格式化
                return this.sum.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            }
        },
        watch: {},
        mounted() {
        }
    }
</script>

