import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import App from './App.vue'
import VueRouter from 'vue-router';
import Home from './components/Home'
import Detail from './components/Detail'
import SuccessList from './components/SuccessList'
import UnsuccessList from './components/UnsuccessList'
import {
    Button,
    Cell,
    CellGroup,
    Col,
    DatetimePicker,
    Dialog,
    DropdownItem,
    DropdownMenu,
    Icon,
    Image,
    ImagePreview,
    List,
    Loading,
    PullRefresh,
    Row,
    Search,
    SwipeCell,
    Tabbar,
    TabbarItem,
    Toast
} from 'vant';


Vue.use(Icon);
Vue.use(Cell).use(CellGroup).use(SwipeCell).use(ImagePreview).use(Image).use(Toast).use(Loading).use(Dialog).use(DatetimePicker);
Vue.use(Row).use(Col).use(Search).use(DropdownMenu).use(DropdownItem).use(PullRefresh).use(List).use(Tabbar).use(TabbarItem).use(Button);
Vue.use(VueRouter);
Vue.use(VueAxios, axios)
Vue.config.productionTip = false
const router = new VueRouter({
    routes: [
        {
            path: '/', component: Home, children: [
                {path: '/successList', component: SuccessList},
                {path: '/unsuccessList', component: UnsuccessList}
            ]
        },
        {path: '/successList/detail', component: Detail}
    ]
})
new Vue({
    render: function (h) {
        return h(App)
    },
    router
}).$mount('#app')
