<template>
    <div style="background-color: #ebebeb;">
        <van-pull-refresh v-model="refreshLoading" @refresh="refresh">
            <van-list style="min-height:calc(100vh - 50px)" v-model="loading" :finished="finished" @load="loadList" finished-text="没有更多了"
                      error-text="加载出错">
                <ul>
                    <li v-for="(item,invoiceIndex) in list" v-bind:key="item.id">
                        <van-swipe-cell class="my-swipe-cell">
                            <div class="invoice-item">
                                <div class="parent">
                                    <div class="left">
                                        <van-image width="100" height="100" :src="item.imageZoomUrl" fit="contain" @click="preview(invoiceIndex)">
                                        </van-image>
                                    </div>
                                    <div class="right">
                                        <template v-if="item.resultStatus === 'success'">
                                            <van-cell title="状态" value="识别成功"/>
                                        </template>
                                        <template v-else-if="item.resultStatus === 'success_exist'">
                                            <van-cell title="状态" value="该票据已经存在"/>
                                        </template>
                                        <template v-else-if="item.resultStatus === 'fail'">
                                            <van-cell title="状态" value="识别失败"/>
                                        </template>
                                        <template v-else-if="item.resultStatus === 'processing'">
                                            <van-cell title="状态" value="识别中"/>
                                        </template>
                                        <template v-else>
                                            <van-cell title="状态" value="状态错误"/>
                                        </template>
                                        <van-cell title="创建时间" v-model="item.createTime"/>
                                        <van-cell v-if="item.resultStatus === 'fail'" title="失败原因" v-model="item.failReason"/>
                                    </div>
                                </div>
                            </div>
                            <van-button square slot="right" type="danger" text="删除" @click.prevent="removeInvoice(item.id,invoiceIndex)"/>
                        </van-swipe-cell>
                    </li>
                </ul>
            </van-list>
        </van-pull-refresh>
        <van-image-preview v-model="showImagePreview" :images="previewImages"
                           :loop="false" :show-indicators="false" :start-position="currentIndex"></van-image-preview>
    </div>
</template>

<script>
    export default {
        name: "UnsuccessList",
        data() {
            return {
                list: [], loading: false, finished: false, error: false, rowCount: 10, //list
                showImagePreview: false, previewImages: [], currentIndex: 0,
                refreshLoading: false
            }
        },
        methods: {
            loadList() {
                let url = mobileURL + '/r/jd?sid=' + sid + '&cmd=com.actionsoft.apps.addons.invoice.queryUnsuccessInvoiceList' +
                    '&skip=' + this.list.length + '&rowCount=10'
                this.axios.get(url).then((res) => {
                    let ro = res.data
                    if (ro.result === 'ok') {
                        this.list.push(...ro.data.list)
                        this.previewImages.push(...ro.data.list.map(item => item.imageUrl))
                        // 加载状态结束
                        this.loading = false
                        // 数据全部加载完成
                        if (ro.data.list.length === 0) {//不能用不足一页判断，只能多取一次因为有时候是9条（一次取10条，有9条符合条件）
                            this.finished = true
                        }
                    } else {
                        this.error = true
                    }
                    this.refreshLoading = false;//如果是下拉刷新，则置回false状态
                })
            },
            reloadList() {
                this.list = []
                this.loading = true
                this.finished = false
                this.loadList()
            },
            preview(currentIndex) {
                this.showImagePreview = true;
                this.currentIndex = currentIndex
            },
            refresh() {
                this.previewImages = []
                this.reloadList()
            },
            removeInvoice(invoiceId, index) {
                this.$dialog.confirm({
                    title: '请确认',
                    message: '确认删除该票据吗'
                }).then(() => {
                    this.$toast.loading({
                        duration: 0,       // 持续展示 toast
                        forbidClick: true, // 禁用背景点击
                        loadingType: 'spinner',
                        message: '正在删除...'
                    });
                    let url = mobileURL + '/r/jd?sid=' + sid + '&cmd=com.actionsoft.apps.addons.invoice.removeInvoice&invoiceId=' + invoiceId
                    this.axios.get(url).then((res) => {
                        let ro = res.data
                        if (ro.result === 'ok') {
                            this.$toast.success('删除成功');
                            let deletedInvoice = this.list[index];
                            //前端删除当前发票
                            this.list.splice(index, 1)
                        } else {
                            this.$toast.fail(ro.msg);
                        }
                    })
                }).catch(() => {
                    // on cancel
                });
            }
        }
    }
</script>

<style scoped>
    .invoice-item {
        margin: 15px 10px;
        padding: 10px;
        background-color: #ffffff;
        border-radius: 5px;
    }

    .parent {
        display: flex;
    }

    .left {
        width: 100px;
    }

    .right {
        flex: 1;
    }

    /deep/ .van-swipe-cell__right {
        display: flex;
        align-items: center;
    }
</style>